import uniSwiperDot from "@/components/uni-swiper-dot/uni-swiper-dot.vue"
// 瀑布流组件
import WaterfallFlow from '@/components/nairenk-waterfall-flow/nairenk-waterfall-flow.vue';
import {
	mapState,
	mapMutations
} from "vuex"
export default {
	components: {
		uniSwiperDot,
		WaterfallFlow
	},
	computed: mapState([
		'hasLogin',
	]),
	data() {
		return {
			interval: 5000,
			duration: 3500,
			premargin: 45,
			nextmargin: 45,
			info: [], //轮播图信息
			current: 0,
			mode: 'indexes',
			page: 1,
			start: 0,
			end: 0,
			list: [], // 列表
			loading: true,
			listAll: [], //接口获取列表
			chrList: [], //首页子级入口展示
		}
	},
	onReachBottom() {
		this.page++;
		this.loading = true;
		this.getList();
	},
	methods: {
		...mapMutations([
			'saveUserMsgs'
		]),
		// 轮播图事件
		change(e) {
			this.current = e.detail.current;
		},
		// 选中
		choose(item) {
			// item 返回选中 JSON 对象
			// console.log("===", item)
			uni.navigateTo({
				url: '../productDetail/productDetail?goodsid='+item.id,
			});
		},
		// 获取课程列表
		async getClassList() {
			await this.$http.getClassList({}).then(res => {
				this.listAll = res.data
			})
		},
		// 模拟加载数据
		getList() {
			if (this.list.length < this.listAll.length) {
				setTimeout(() => {
					this.end = this.page * 5;
					this.list = this.list.concat(this.listAll.slice(this.start, this.end));
					this.start = this.end;
					// 延迟 120 毫秒隐藏加载动画，为了跟组件里面的 100 毫秒定位有个平缓过度
					setTimeout(() => {
						this.loading = false;
					}, 120);
				}, 1000)
			} else {
				this.loading = false;
			}
		},
		// 进入精品课程
		gotoJP(e) {
			uni.navigateTo({
				url: 'jingpingClass?class=' + e,
			});
		},
	},
	async onLoad() {
		this.$http.getLunImgs({}).then(res => {
			this.info = res.data
		});
		this.$http.getIndexChr({}).then(res => {
			this.chrList = res.data
		})
		this.$http.lookUserMsgs({
			uid: uni.getStorageSync('uid')
		}).then(res => {
			console.log("userMsgs", res.data);
			if (res.data != 0) {
				this.saveUserMsgs(res.data)
			}
		})
		await this.getClassList();
		await this.getList();
	}
}
